@import url(https://fonts.googleapis.com/css2?family=Jost:wght@300;400;500;700&display=swap);
*,*::before,*::after{box-sizing:border-box;margin:0;padding:0}html{-webkit-font-smoothing:antialiased;-webkit-text-size-adjust:100%;overflow-x:hidden}p{padding:0.5rem 0}:root{--black: #323E42;--box-shadow: 0 0 0.5rem rgba(0,0,0,0.1);--purple: #C55FFC;--purple-dark: #7954A1;--purple-light: #EFDCF9;--white: #FFF}body{background-color:#F4F6F8;color:#323E42;color:var(--black);font-family:'Jost', sans-serif;font-size:18px;font-weight:400;line-height:1.5}.button,.button:visited,button.button{border:0;cursor:pointer;background-color:#7954A1;background-color:var(--purple-dark);color:#FFF;color:var(--white);transition:ease-in-out 0.3s background-color;padding:0.5rem 1rem;border-radius:4px;font-size:1.25rem}.button:focus,.button:hover,.button:visited:focus,.button:visited:hover,button.button:focus,button.button:hover{background-color:#7954A1;background-color:var(--purple-dark);-webkit-filter:brightness(1);filter:brightness(1)}.button.disabled,.button:visited.disabled,button.button.disabled{background-color:#323E42;background-color:var(--black);cursor:not-allowed;pointer-events:none}.header__wrapper{width:100%;margin:0 auto;max-width:1080px;padding:1rem;display:flex;justify-content:space-between;align-items:center;flex-wrap:wrap}@media only screen and (max-width: 900px){.header__wrapper{text-align:left;justify-content:center}}.header__heading{margin-right:1.5rem}@media only screen and (max-width: 900px){.header__heading{padding-bottom:0.5rem}}main{width:100%;margin:0 auto;max-width:1080px;padding:1rem}.click-tap:before{content:"click"}@media only screen and (max-width: 900px){.click-tap:before{content:"tap"}}.camera{font-family:videojs-record;font-weight:normal;font-style:normal}.camera:before{content:"\f102"}.record{font-family:videojs-record;font-weight:normal;font-style:normal}.record:before{content:"\f105"}.play{font-family:VideoJS;font-weight:normal;font-style:normal}.play:before{content:"\f101"}#video{grid-area:video;font-size:18px;border-bottom-left-radius:4px;width:100%}.video__howto{grid-area:howto;background-color:#FFF;background-color:var(--white);padding:1rem;border-bottom-right-radius:4px}.video__howto ol{padding-left:1.2rem}.video__button{position:absolute;top:0;left:0;width:100%;background-color:rgba(255,255,255,0.1);text-align:center;padding:1rem}.main .video-js .vjs-control-bar{display:flex;align-items:center;background-color:#323E42;background-color:var(--black);border-bottom-left-radius:4px}.main .vjs-record .vjs-device-button.vjs-control{box-shadow:none;background-color:#323E42;background-color:var(--black);border-radius:4px;font-size:1.5rem}.video__content{background-color:#7954A1;background-color:var(--purple-dark);border-top-left-radius:4px;border-top-right-radius:4px;color:#FFF;color:var(--white);padding:0.25rem 1rem;grid-area:content}.video__wrapper{display:grid;grid-template-columns:66.666% 33.333%;grid-template-rows:auto 480px;grid-gap:0px 0px;gap:0px 0px;grid-template-areas:"content content" "video howto"}@media only screen and (max-width: 900px){.video__wrapper{display:flex;flex-direction:column}}

